@import "../../inc/vars";
.wp-block-acrobits-menu-group-title:last-child {
	margin-bottom: 14px;
	@media (min-width: $screen-sm) {
		margin-bottom: 38px;
	}
	@media (min-width: 1310px) {
		margin-bottom: 0;
	}
}
.menu-group-title {
	padding: 14px;
	border-radius: 6px;
	background: #FFF;
	box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
	margin: 0 0 4px 0;
	@media (min-width: 1310px){
		padding: 0;
		box-shadow: none;
		border-radius: 0;
		background: none;
		margin: 0 0 24px 0;
	}
	a {
		font-size: 18px !important;
		font-weight: 500 !important;
		line-height: 20px !important;
		color: #4F4B58 !important;
		padding: 0 !important;
		text-decoration: none !important;
		&:hover {
			color: $purple !important;
		}
	}
}
.wp-block-acrobits-menu-group-title:last-child {
	.menu-group-title {
		margin-bottom: 0 !important;
	}
}